#informacion2 {
  background-color: $white;
  text-align: center;
  .content-img-1 {
    text-align: left;
    .img-1 {
      width: 70%;
    }
  }
  .content-img-2 {
    text-align: right;
    margin-top: -35%;
    // transform: translateY(-75%);
    .img-2 {
      width: 62%;
      padding: 5px;
      background-color: $white;
      border-radius: 20px;
      box-shadow: 0 0 20px 0 $gray3;
    }
  }
  h2 {
    font-family: "MyriadRoman";
    font-weight: bold;
    margin-bottom: 1.5rem;
    color: $red;
    @media (min-width: 992px) {
      font-size: 3rem;
    }
  }
  p {
    color: $black;
    @media (min-width: 992px) {
      font-size: 2rem;
    }
  }
  .text {
  }
}