@import "../variables";

#welcome {
  position: relative;
  background-image: url(../images/bg-welcome-int-2.png);
  background-position: bottom center;
  background-repeat: no-repeat;
  background-size: cover;
  padding-top: calc(75px + 3rem);
  padding-bottom: 1rem;
  @media (min-width: 992px) {
    padding-top: calc(100px + 2rem);
  }
  @media (min-width: 1600px) {
    padding-top: calc(100px + 5rem);
  }
  .content-list {
    display: flex;
    flex-direction: column;
    justify-content: center;
    .list {
      margin-bottom: 2rem;
      display: flex;
      align-items: center;
      @media (min-width: 992px) {
        margin-bottom: 5rem;
      }
      @media (min-width: 1400px) {
        margin-left: 10%;
      }
      .circle {
        position: relative;
        width: 3rem;
        height: 3rem;
        background: $white;
        border-radius: 50%;
        display: inline-table;
        @media (min-width: 992px) {
          width: 4rem;
          height: 4rem;
        }
        h2 {
          margin-bottom: 0;
          color: $red;
          text-align: center;
          position: relative;
          top: 50%;
          transform: translateY(-50%);
          @media (min-width: 992px) {
            font-size: 3rem;
          }
        }
      }
      .text {
        padding-left: 1rem;
        text-align: left;
        h2 {
          font-family: "MyriadRoman";
          font-weight: bold;
          margin-bottom: 0;
          color: $white;
          @media (min-width: 992px) {
            font-size: 3rem;
          }
        }
      }
    }
    .download-app {
      position: relative;
      width: 90%;
      margin: 0 auto 2rem;
      padding: 2rem 1rem;
      background-color: $white;
      border-radius: 15px;
      box-shadow: 5px 0px 25px 0px $gray2;
      @media (min-width: 1200px) {
        width: 60%;
        margin: 0 auto;
      }
      h2 {
        font-family: "MyriadRoman";
        font-weight: bold;
        color: $black;
        text-align: center;
        margin-bottom: 1rem;
        @media (min-width: 992px) {
          font-size: 2rem;
        }
        span {
          font-style: italic;
          color: $red;
        }
      }
      a {
        img {
          transition: all 0.3s;
        }
        &:hover {
          img {
            transform: scale(1.2);
          }
        }
      }
    }
  }
  .img-welcome {
    width: 50%;
    @media (min-width: 768px) {
      width: 100%;
    }
    @media (min-width: 1400px) {
      width: 60%;
    }
  }
  .access-player {
    position: relative;
    width: 80%;
    margin: 2rem auto;
    padding: 0.5rem;
    background-color: $white;
    border-radius: 5px;
    button {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 100px;
      height: 100px;
      border: none;
      border-radius: 50px;
      background-color: rgba($color: $gray2, $alpha: 0.6);
      transform: translate(-50%, -50%);
      &:hover {
        img {
          transform: scale(1.3);
          filter: drop-shadow(3px 3px 3px $gray2);
        }
      }
      div {
        position: absolute;
        top: 50%;
        left: 50%;
        width: 70px;
        height: 70px;
        border: 2px solid $white;
        border-radius: 50px;
        transform: translate(-50%, -50%);
        display: flex;
        align-items: center;
        justify-content: center;
        img {
          margin-left: 5px;
          width: 40px;
          transition: all 0.3s;
        }
      }
    }
  }
  a.btn {
    font-size: 1.5rem;
    font-weight: 300;
    padding: 1rem 3rem;
  }
  ul {
    margin-top: 1rem;
    margin-bottom: 0rem;
    padding: 0;
    text-align: center;
    li {
      display: inline-block;
      a {
        color: $black;
        padding: 1rem;
        transition: all 0.3s;
        @media (min-width: 992px) {
          padding: 1rem 3rem;
        }
        &:hover {
          color: $red;
        }
        img {
          margin-right: 0.3rem;
          vertical-align: text-top;
          height: 16px;
        }
      }
    }
  }
}