@import "components/variables";

.navbar.nav-white {
  .navbar-brand {
    .img-logo {
      background-image: url(../images/logo-nav-white.png);
    }
  }
  ul {
    .nav-item {
      a {
        color: $white;
        &.active {
          color: $white;
        }
        &:before {
          background: $white;
        }
      }
      &:hover {
        a {
          color: $white;
        }
      }
    }
  }
}

section {
  padding-top: 3rem;
  padding-bottom: 3rem;
  @media (min-width: 992px) {
    padding-top: 5rem;
    padding-bottom: 5rem;
  }
}

@import "components/internas/welcome2";
#welcome {
  padding-top: calc(75px + 1rem);
}
.phone-welcome-mobile {
  // position: absolute;
  width: 30vw;
  margin: 0 auto;
  padding-bottom: 1rem;
  // top: 0;
  // right: 15px;
  // z-index: 0;
}

#caracteristicas {
  .content-icon {
    width: 20vw;
    padding: 10px;
    margin: 0 auto;
    box-shadow: 5px 5px 10px 0 $gray2;
    @media (min-width: 992px) {
      width: 10vw;
      padding: 1rem;
    }
    img {
      width: 70%;
      margin-bottom: 0.5rem;
    }
    p {
      font-size: 0.6rem;
      color: $gray2;
      margin: 0;
      line-height: 1;
      @media (min-width: 460px) {
        font-size: 0.8rem;
      }
      @media (min-width: 768px) {
        font-size: 1.2rem;
      }
    }
  }
  .center {
    position: relative;
    .circle-center {
      position: absolute;
      top: 50%;
      left: 50%;
      border-radius: 50%;
      z-index: -1;
      transform: translateY(-50%) translateX(-50%);
    }
    .circle-1 {
      width: 38vw;
      height: 38vw;
      background-color: rgba($color: $red, $alpha: 0.4);
      @media (min-width: 992px) {
        width: 33vw;
        height: 33vw;
      }
    }
    .circle-2 {
      width: 34vw;
      height: 34vw;
      background-color: rgba($color: $red, $alpha: 0.7);
      @media (min-width: 992px) {
        width: 29vw;
        height: 29vw;
      }
    }
    .circle-3 {
      width: 30vw;
      height: 30vw;
      background-color: rgba($color: $red, $alpha: 1.0);
      @media (min-width: 992px) {
        width: 25vw;
        height: 25vw;
      }
    }
    .phone-center {
      width: 100%;
      @media (min-width: 768px) {
        width: 70%;
      }
    }
  }
}

@import "components/internas/informacion1";
#informacion1 {
  background-image: none;
  background-color: $gray1;
  h2 {
    font-weight: bold;
  }
  p {
    color: $gray2;
  }
}

@import "components/internas/informacion2";

#informacion2 {
  h2 {
    font-weight: bold;
  }
  p {
    color: $gray2;
  }
}

#procesos {
  background-image: url(../images/int3/bg-procesos.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  .lateral {
    @media (min-width: 992px) {
      padding: 0 5%;
    }
  }
  .box-button {
    font-family: "MyriadRoman";
    text-align: center;
    padding: 1rem;
    color: $white;
    font-size: 1.5rem;
    border: 1px solid $white;
    border-radius: 10px;
    cursor: pointer;
    transition: all 0.3s;
    &:hover {
      color: $red;
      border-color: $white;
    }
    &:before {
      background: $white;
      border-radius: 10px;
    }
  }
  h3 {
    font-family: "MyriadRoman";
    font-weight: bold;
    margin-bottom: 3rem;
    color: $white;
    border-bottom: 1px solid $white;
    padding-bottom: 0.5rem;
    @media (min-width: 992px) {
      font-size: 2rem;
    }
  }
  p {
    color: $white;
    @media (min-width: 992px) {
      font-size: 1rem;
    }
  }
}

#download {
  background-color: $white;
  padding-bottom: 0rem;
  padding-top: 0;
  h2 {
    font-family: "MyriadRoman";
    color: $black;
    @media (min-width: 1400px) {
      font-size: 3rem;
    }
    span {
      font-weight: bold;
      color: $red;
    }
  }
  a {
    transition: all 0.3s;
    &:hover {
      transform: scale(1.2);
    }
  }
  .img-store {
    width: 25vw;
    @media (min-width: 992px) {
      width: 13vw;
    }
  }
  .phone-mobile {
    width: 100%;
    @media (min-width: 992px) {
      width: 75%;
    }
  }
  .phone {
    @media (min-width: 992px) {
      position: absolute;
      width: 100%;
      right: 0;
      bottom: -22%;
      z-index: 1;
    }
  }
  .circle-phone {
    @media (min-width: 992px) {
      position: absolute;
      top: 50%;
      left: 50%;
      width: 30vw;
      height: 30vw;
      background-color: $gray1;
      border-radius: 50%;
      transform: translateY(-50%) translateX(-50%);
    }
  }
  .container-fluid {
    background-color: $bg-black;
    position: relative;
    .container {
      .border-lr {
        @media (min-width: 576px) {
          border-left: 1px solid $gray2;
          border-right: 1px solid $gray2;
        }
      }
      h2 {
        font-family: "MyriadRoman";
        text-align: center;
        color: $red;
        font-weight: bold;
        font-size: 3rem;
      }
      p {
        font-family: "MyriadRoman";
        text-align: center;
        color: $white;
        margin: 0;
      }
    }
  }
}