#informacion1 {
  background-image: url(../images/int1/bg-info1.png);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  text-align: center;
  h2 {
    font-family: "MyriadRoman";
    font-weight: bold;
    margin-bottom: 1.5rem;
    color: $red;
    @media (min-width: 992px) {
      font-size: 3rem;
    }
  }
  p {
    color: $white;
    @media (min-width: 992px) {
      font-size: 2rem;
    }
  }
  img {
    @media (max-width: 767.98px) {
      width: 60% !important;
    }
  }
  .text {
    @media (max-width: 767.98px) {
      margin-top: 1rem; 
    }
  }
}